import {ActionType, StateType, TaskType} from "./TodolistApp";


export const reducer = (prevState: StateType[], action: ActionType): StateType[] => {
    switch (action.type) {
        case 'ADD-TODO':
            return [...prevState, {
                idList: action.idList,
                listTitle: action.listTitle ?? '',
                filter: 'all',
                tasks: []
            }];
        case 'REMOVE-LIST':
            return prevState.filter(item => item.idList !== action.idList);
        case 'ADD-TASK':

            return prevState.map((item) =>
                (action.idList === item.idList ? {
                    ...item,
                    tasks: [...item.tasks, {
                        idTask: action.idTask ?? '',
                        taskTitle: action.taskTitle ?? '',
                        isDone: false
                    }]
                } : item))

        case 'REMOVE-TASK':
            return prevState.map((item: StateType) => item.idList === action.idList ? {
                ...item,
                tasks: item.tasks.filter((task: TaskType) => task.idTask !== action.idTask)
            } : item)


        case 'CHECKBOX-TOG':
            return prevState.map((item: StateType) =>
                item.idList === action.idList
                    ? {
                        ...item,
                        tasks: item.tasks.map((task: TaskType) =>
                            task.idTask === action.idTask
                                ? { ...task, isDone: !task.isDone }
                                : task
                        ),
                    }
                    : item
            );
        default:
            throw new Error('Unknown action');
    }
}