import styled from "styled-components";
import {Setting} from "./Setting";
import React, {ChangeEvent, useEffect, useState} from "react";
import {Counter} from "./Counter";
import {Buttons} from "./Buttons";

const LOCAL_STORAGE_KEY = "counterAppData";
export const CounterApp = () => {

    const [count, setCount] = useState(0)
    const [min, setMin] = useState('')
    const [max, setMax] = useState('')
    const [buttonSetState, setButtonSetState] = useState(false)
    const [counterState, setCounterState] = useState(true)
    const [message, setMessage] = useState('Set values')

    useEffect(() => {
        const dataFromLocalStorage = localStorage.getItem(LOCAL_STORAGE_KEY);
        if (dataFromLocalStorage) {
            const savedData = JSON.parse(dataFromLocalStorage);
            setCount(savedData.count);
            setMin(savedData.min);
            setMax(savedData.max);
            setMessage(savedData.message)
        }
    }, []);

    useEffect(() => {
        const dataToSave = {
            count,
            min,
            max,
            message
        };

        localStorage.setItem(LOCAL_STORAGE_KEY, JSON.stringify(dataToSave));
    }, [count, min, max]);


    const setMinHandler = (event: ChangeEvent<HTMLInputElement>) => {
        const newMin = event.target.value.replace(/[^0-9]/g, '');
        setMin(newMin);
        setCounterState(true);
        if (+max > +newMin || !newMin ) {
            setButtonSetState(true);
            setMessage('Set values');
        } else {
            setButtonSetState(false);
            setMessage('Incorrect input');
        }
    };

    const setMaxHandler = (event: ChangeEvent<HTMLInputElement>) => {
        const newMax = event.target.value.replace(/[^0-9]/g, '');
        setMax(newMax);
        setCounterState(true);
        if ( +newMax > +min || !newMax) {
            setButtonSetState(true);
            setMessage('Set values');
        } else {
            setButtonSetState(false);
            setMessage('Incorrect input');
        }
    };

    const addCount = () => {
        if (count >= +min && count <= +max - 1)
            setCount(count + 1)
    }


    const resetCount = () => {
        setCount(+min)


    }
    const setCounterHandler = () => {
        setCount(0)
        setCount(+min)
        setButtonSetState(false)
        setCounterState(false)
        setMessage('ok')

    }

    let ifActiveIncButton = count >= +min && count <= +max - 1 && +min>-1 && +max>0 && !buttonSetState
    let ifActiveResetButton = count>0
    let ifRedCount = count === +max && count !== +min



    return<>
    <Title>
        <div>This component increases the number by one within the specified limits</div>

    </Title>
<Wrapper>
<SettingFrame>
        <Setting setMinHandler={setMinHandler}
                 setMaxHandler={setMaxHandler}
                 min={min}
                 max={max}
                 setCounterHandler={setCounterHandler}
                 buttonSetState={buttonSetState}
                 message={message}/>
</SettingFrame>
        <CounterFrame>
            <Countframe>
            {message==='ok' ? <Counter ifRedCount={ifRedCount} count={count}/> : <div>{message}</div>}
            </Countframe>
            <ButtonsFrame>
                <Buttons addCount={addCount} ifActiveResetButton={ifActiveResetButton} ifActiveIncButton={ifActiveIncButton}
                     resetCount={resetCount}/>
            </ButtonsFrame>
        </CounterFrame>
</Wrapper>
</>
}

const Wrapper = styled.div`
  
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
text-align: center;
  justify-content: center;
`

const SettingFrame = styled.div`
  width: 300px;
  height: 200px;
  border: solid;
  margin-right: 20px;
  margin-top: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);

`

const CounterFrame = styled.div`
  display: flex;
  flex-direction: column;
  border: solid;
  width: 300px;
  height: 200px;
  margin-right: 20px;
  margin-top: 10px;
`
const Countframe = styled.div`
  margin: 12px;
  border: solid lightblue;
  text-align: center;
  width: 86%;
  height: 50%;
    
`
const ButtonsFrame = styled.div`
  display: flex;
  flex-direction: row;

  margin: 12px;
  border: solid lightblue;
  width: 86%;
  height: 50%;
`



const Title = styled.div`
font-weight: bold ;
  text-align: center;
`