import React, {useReducer} from 'react';
import styled from 'styled-components';

type StateType = {
    name: string
    age: number
    girlprop: { one: string }
}
type ActionType = {
    type: string
}

const reducer = (prevState: StateType[], action: ActionType) => {
    switch (action.type) {
        case 'CHANGE-OLA':
            return prevState.map((item) => item.name === 'ola'?
                {...item, girlprop:{...item.girlprop, one:'tonice'} }
                :item )
        default:
            return prevState
    }
}

export const Practice = () => {

    const arr: StateType[] = [{
        name: 'ola',
        age: 19,
        girlprop: {one: 'teysty'}
    }, {
        name: 'klubnika',
        age: 38,
        girlprop: {one: 'lovely'}
    }]

    const [state, action] = useReducer(reducer, arr)


    const btn = () => {
        action({type: 'CHANGE-OLA'})
    }

    return (
        <Wrapper>

        </Wrapper>
    );
}

const Wrapper = styled.div`

`;