import React from 'react';
import App from './App';
import { createRoot } from 'react-dom/client';
import "./index.css"
import {BrowserRouter} from "react-router-dom";


const container  = document.getElementById('root') as HTMLElement
const root = createRoot(container);
root.render(
    <BrowserRouter>
        <App />
    </BrowserRouter>
);