import React, {useReducer, ChangeEvent, useState} from 'react';
import {v1} from 'uuid';
import {reducer} from "./todoReducer";
import {Todolist} from "./Todolist";
import {InputForm} from "./InputForm";
import styled from "styled-components";

export type TaskType = {
    idTask: string;
    taskTitle: string;
    isDone: boolean;
};

export type StateType = {
    idList: string;
    listTitle: string;
    filter: string;
    tasks: TaskType[];
};

export type ActionType = {
    type: string;
    listTitle?: string;
    taskTitle?: string
    idList: string
    idTask?: string
};


export const TodolistApp = () => {

    const [state, action] = useReducer(reducer, []);
    const addTodolist = (trimmedValue: string) => {
        action({type: 'ADD-TODO', listTitle: trimmedValue, idList: v1()})
    }
    return (
        <Wrapper>
            <InputForm addFromInput={addTodolist} defaultInput={'New list'}/>
            <Lists>
            {state.map((item) => <Todolist
                idList={item.idList}
                listTitle={item.listTitle}
                filter={item.filter}
                tasks={item.tasks}
                action={action}
            />)}
            </Lists>
        </Wrapper>
    )
}


export default TodolistApp;

const Wrapper = styled.div`
    
`

const Lists = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
`